-----------*    $MEDIA QUERY TESTS\n\\*------------------------------------ */\n\nbody::before {\n  display: block;\n  position: fixed;\n  z-index: 100000;\n  background: black;\n  bottom: 0;\n  right: 0;\n  padding: 0.5em 1em;\n  content: 'No Media Query';\n  color: rgba(255, 255, 255, 0.75);\n  border-top-left-radius: 10px;\n  font-size: 0.75em;\n}\n\n@media print {\n  body::before {\n    display: none;\n  }\n}\n\nbody::after {\n  display: block;\n  position: fixed;\n  height: 5px;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  z-index: 100000;\n  content: '';\n  background: black;\n}\n\n@media print {\n  body::after {\n    display: none;\n  }\n}\n\n@media (min-width: 351px) {\n  body::before {\n    content: 'xsmall: 350px';\n  }\n\n  body::after,\n  body::before {\n    background: dodgerblue;\n  }\n}\n\n@media (min-width: 501px) {\n  body::before {\n    content: 'small: 500px';\n  }\n\n  body::after,\n  body::before {\n    background: darkseagreen;\n  }\n}\n\n@media (min-width: 701px) {\n  body::before {\n    content: 'medium: 700px';\n  }\n\n  body::after,\n  body::before {\n    background: lightcoral;\n  }\n}\n\n@media (min-width: 901px) {\n  body::before {\n    content: 'large: 900px';\n  }\n\n  body::after,\n  body::before {\n    background: mediumvioletred;\n  }\n}\n\n@media (min-width: 1101px) {\n  body::before {\n    content: 'xlarge: 1100px';\n  }\n\n  body::after,\n  body::before {\n    background: hotpink;\n  }\n}\n\n@media (min-width: 1301px) {\n  body::before {\n    content: 'xxlarge: 1300px';\n  }\n\n  body::after,\n  body::before {\n    background: orangered;\n  }\n}\n\n@media (min-width: 1501px) {\n  body::before {\n    content: 'xxxlarge: 1400px';\n  }\n\n  body::after,\n  body::before {\n    background: dodgerblue;\n  }\n}\n\n/* ------------------------------------*    $GENERIC\n\\*------------------------------------ */\n\n/* ------------------------------------*    $RESET\n\\*------------------------------------ */\n\n/* Border-Box http:/paulirish.c